<template>
  <div>
    <div class="aboutus-businessscope">
      <div class="aboutus-businessscope-title">
        <span>业务范围</span>
        <hr style="border: none;height: .6vh;background:-webkit-linear-gradient(left,#5DD9F4,#6256EB);width: 3vw;"/>
      </div>
      <div class="aboutus-businessscope-text">
        <div class="aboutus-businessscope-text-one">
          <span>公司产品应用场景丰富，可根据<span style="font-weight: bold">机场、电力、港口、轨交、军工、市政等行业</span>特点和要求，为客户量身定制最佳的绿色智慧能源一体化解决方案，提供业界领先的绿色智慧能源产品和服务。</span>
        </div>
        <div class="aboutus-businessscope-text-two">
          <div class="aboutus-businessscope-text-two-img">
            <img src="../static/img/aboutus-businessscope.png" alt=""/>
          </div>
          <div class="aboutus-businessscope-text-two-text">
            <span>&emsp;&emsp;公司“绿色微储飞机地面电源系统”现已在多个大型机场投入运行，按当前部署进度，<span style="font-weight: bold;color: cornflowerblue">到2019年底，公司每年可实现为航空公司、机场减少APU开启时间275,940小时，节约航油41,391吨，减少二氧化碳排放量131,623吨，节约资金31,174万元。</span></span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<style lang="less" scoped>
  .aboutus-businessscope {
    position: relative;
    /*top: 5vh;*/
    height: 87vh;
    padding-bottom: 5vh;
    font-family: 思源黑体;

    .aboutus-businessscope-title {
      width: 100%;
      font-size: .2rem;
      color: #404040;
    }

    .aboutus-businessscope-text {
      position: relative;
      .aboutus-businessscope-text-one {
        position: relative;
        width: 86%;
        left: 7%;
        top: 7vh;
        span {
          font-size: .12rem;
          line-height: 70px
        }
      }
      .aboutus-businessscope-text-two{
        position: relative;
        top: 9vh;
        .aboutus-businessscope-text-two-img{
          width: 40%;
          float: left;
          img{
            width: 100%;
          }
        }
        .aboutus-businessscope-text-two-text {
          width: 45%;
          position: relative;
          right: 7%;
          float: right;
          padding: 150px 0 150px 100px;
          text-align: justify;
          span{
            font-size: .12rem;
            line-height: 50px
          }
        }
      }
    }
  }

</style>

<script>
  export default {
    name: "aboutus-businessscope"
  }
</script>



