<template>
  <div>
    <div class="main-contact">
      <div class="main-contact-text">
        <div class="main-contact-text-title">
          <span>联系我们</span>
          <br>
          <hr style="border: none;height: .5vh;background:-webkit-linear-gradient(left,#5DD9F4,#6256EB);width: 4vw;position: relative;float: left"/>
        </div>
        <div class="main-contact-text-info">
          <br>
          <span style="font-family: 'Avenir', Helvetica, Arial, sans-serif;font-size: .15rem">028 - 65790186</span>
          <br>
          <span style="font-family: 'Avenir', Helvetica, Arial, sans-serif;font-size: .15rem">aeme@scaeme.cn</span>
          <br>
          <span style="font-size: .13rem">成都市成华区龙潭工业园航天路50号国机西南大厦17楼A区</span>
        </div>
        <div class="main-contact-text-click">
          <div class="main-contact-text-click-text" @click="building()">在线留言</div>
        </div>
      </div>
      <div class="main-contact-pic">
        <img src="../static/img/main-contact.png" alt="">
      </div>
    </div>
  </div>
</template>

<style scoped>
  .main-contact-text-click-text:hover{
    /*width: 6.5vw;*/
    padding: 15px 15px;
    font-weight: bold;
  }
  .main-contact-text-click-text{
    background: linear-gradient(-45deg,#5DD9F4,#6256EB);
    padding: 10px 10px;
    color: white;
    width: 6vw;
    text-align: center;
    border-radius: 50px;
    transition: all .3s;
  }
  .main-contact-text-click{
    margin-top: 7vh;
    text-align: left;
    position: relative;
    left: 11vw;
    cursor: pointer;
  }

  .main-contact-pic img {
    width: 100%;
  }

  .main-contact-pic {
    width: 60%;
    position: relative;
    right: 0;
    float: right;
  }

  .main-contact-text-info {
    line-height: 5vh;
    text-align: left;
    position: relative;
    left: 11vw;
    top: 1vh;
  }

  .main-contact-text-title {
    width: 100%;
    font-size: .2rem;
    color: #404040;
    position: relative;
    left: 11vw;
    text-align: left;
    height: 7vh;
  }

  .main-contact-text {
    width: 30%;
    float: left;
    position: relative;
    top: 15vh;
    height: 100%;
  }

  .main-contact {
    margin-top: 21vh;
    height: 82vh;
  }

  * {
    font-family: 思源黑体;
  }
</style>

<script>
  export default {
    name: "main-contact",
    methods:{
      building(){
        const building = this;
        building.$router.push({name:"Building"})
      }
    }
  }
</script>





