import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/pages/index.vue'
import AboutAEME from '@/pages/aboutus/aboutus.vue'
import Building from '@/publiccomponents/public-building.vue'


import test from '@/pages/test.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/t',
      name: 'test',
      component: test
    },
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    {
      path: '/aboutus',
      name: 'AboutAEME',
      component: AboutAEME
    },
    {
      path: '/building',
      name: 'Building',
      component: Building
    }
  ]
})



