<template>
<div>
  <div class="main-aboutus">
    <div class="main-aboutus-text">
      <span>四川航电微能源有限公司是一家致力于绿色智慧能源发展与应用的创新型公司</span><br>
      <span>秉承“让绿色能源无处不在”的企业愿景</span><br>
      <span>为客户量身定制最佳的绿色智慧能一体化解决方案</span><br>
    </div>
    <div class="main-aboutus-pic">
      <div class="pic-1">
        <img src="../static/img/main-img-aboutus.png" alt="" @click="aboutus()">
      </div>
      <div class="pic-2">
        <img src="../static/img/main-img-company-environment.png" alt="" @click="aboutus_offenvironment()">
      </div>
    </div>
  </div>
</div>
</template>

<style scoped>

  .main-aboutus-pic img:hover{
    transform: scale(1.2);
  }

  .main-aboutus-pic img{
    width: 100%;
    float: left;
    cursor: pointer;
    transition: all 0.6s;
  }
  .pic-1,.pic-2{
    width: 40%;
    height: 100%;
    float: left;
    padding: 0 5% 5vh 5%;
  }
  .main-aboutus-pic{
    padding: 12vh 5vw;
  }
  .main-aboutus-text span{
    padding: 1vh 0;
    line-height: 7vh;
    font-size: .15rem;
    /*font-style: oblique;*/
    color: #404040;
  }
  .main-aboutus{
    padding: 15vh 0;
    font-family: 思源黑体;
    height: 72vh;
  }

</style>

<script>
    export default {
        name: "main-aboutus",
      methods:{
          aboutus()
          {
            const aboutus = this;
            aboutus.$router.push({name: "AboutAEME"})
          },
        aboutus_offenvironment()
        {
          const offenvironment = this;
          offenvironment.$router.push({name: "AboutAEME"})
        }
        }
    }
</script>





