<template>
  <div>
    <div class="aboutus-offenvironment">
      <div class="aboutus-odffenvironment-title">
        <span>办公环境</span>
        <hr style="border: none;height: .6vh;background:-webkit-linear-gradient(left,#5DD9F4,#6256EB);width: 3vw;"/>
      </div>
      <div class="aboutus-offenvironment-text">
        <div class="aboutus-offenvironment-text-text">
          <div style="font-size: .14rem;font-weight: bold">工作环境</div>
          <div style="text-align: justify;">
            <span>左侧展示公司办公环境、生产环境等相关图片，并以自动轮播方式实现切换，与此同时本文本框同步切换对应描述。</span>
