<style lang="less" scoped>
  .aboutus-offenvironment {
    position: relative;
    /*top: 5vh;*/
    height: 69vh;
    padding-bottom: 5vh;
    font-family: 思源黑体;

    .aboutus-odffenvironment-title {
      width: 100%;
      font-size: .2rem;
      color: #404040;
    }

    .aboutus-offenvironment-text {
      position: relative;
      top: 5vh;
      width: 86%;
      left: 7%;

      .aboutus-offenvironment-text-text {
        position: relative;
        width: 60%;
        float: left;
        top: 12vh;

        span {
          position: relative;
          top: 5vh;
          font-size: .12rem;
          line-height: 50px;
        }
      }

      .aboutus-offenvironment-text-imgcaronsel {
        position: relative;
        width: 40%;
        float: right;

        img {
          width: 100%;
        }
      }
    }
  }

</style>

<script>
  export default {
    name: "aboutus-offenvironment"
  }
</script>





