<template>
  <div>
    <div class="public-footnote">
      <div class="public-footnote-pic">
        <img src="../assets/footnote-pic.png" alt="">
      </div>
      <div class="public-footnote-text">
        <div class="public-footnote-text-click">
          <span><a href="#" @click="home()">首页</a></span>
          <span><a href="#" @click="building()">产品中心</a></span>
          <span><a href="#" @click="building()">项目案例</a></span>
          <br>
          <span><a href="#" @click="aboutaeme()">关于航电</a></span>
          <span><a href="#" @click="building()">资讯中心</a></span>
          <span><a href="#" @click="building()">加入我们</a></span>
        </div>
        <div class="public-footnote-text-record">
          <span>
            scaeme©2018
          </span>
        </div>
      </div>
    </div>
  </div>
</template>

<style scoped>
  .public-footnote-text-click a:hover {
    /*background-image: -webkit-linear-gradient(-45deg,#5DD9F4,#6256EB);*/
    /*-webkit-background-clip:text;*/
    /*-webkit-text-fill-color:transparent;*/
    /*font-weight: bold;*/
    border-bottom: 2px solid #404040;
    /*border-image: -webkit-linear-gradient(#5DD9F4, #6256EB) 30 30;*/
    /*border-image: -moz-linear-gradient(#5DD9F4, #6256EB) 30 30;*/
    /*border-image: linear-gradient(#5DD9F4, #6256EB) 30 30;*/
    /*transition: .5s;*/
  }

  .public-footnote-text-click a {
    text-decoration: none;
    color: #404040;
    display: block;
    padding-bottom: 9px;
    transition: all .2s;
  }

  .public-footnote-text-record span {
    display: inline-block;
    width: 60%;
  }

  .public-footnote-text-record {
    text-align: center;
    padding-bottom: 5vh;
    width: 100%;
    position: relative;
    top: 9vh;
  }

  .public-footnote-text-click span {
    font-size: .12rem;
    width: 20%;
    padding: 20px 50px;
    text-align-last: justify;
    text-align: justify;
    text-justify: distribute-all-lines;
    display: inline-block;
  }

  .public-footnote-text {
    text-align: center;
    margin-top: 9vh;
    position: relative;
    width: 60%;
    left: 20%;
  }

  .public-footnote-pic img {
    width: 100%;
  }

  .public-footnote-pic {
    width: 100%;
  }

  .public-footnote {
    /*margin-top: 16vh;*/
  }

  * {
    font-family: 思源黑体;
  }
</style>

<script>
  export default {
    name: "public-footnote",
    methods:{
      home(){
        const home = this;
        home.$router.push({name:"Index"})
      },
      aboutaeme(){
        const aboutus = this;
        aboutus.$router.push({name: "AboutAEME"})
      },
      building(){
        const building = this;
        building.$router.push({name:"Building"})
      }
    }
  }
</script>





