<template>
  <div>
    <div class="header" style="z-index: 999">
      <Header/>
    </div>
    <div class="aboutus-caronsel">
      <div class="bigpic">
        <img style="height: 16%" src="../static/img/bottom1.png" alt=""/>
        <img style="height: 13%" src="../static/img/bottom2.png" alt=""/>
      </div>
    </div>
  </div>
</template>

<style lang="less" scoped>
  .bigpic {
    position: relative;
    width: 100%;
    background-image: url("../static/img/company-aeme.jpg");
    background-repeat: no-repeat;
    background-position: center 0;
    min-height: 100vh;
    background-size: cover;
    img{
      position: absolute;
      width: 100%;
      left: 0;
      bottom: -.05vh;
    }
  }

  html,
  body {
    margin: 0;
    padding: 0;
  }
</style>

<script>

  import Header from '../../../publiccomponents/public-header'

  export default {
    name: "aboutus-caronsel",
    components:{
      Header,
    }
  }
</script>



