<template>
  <div>
    <div class="public-builiding">
      <div class="animated fadeInLeft" id="public-builiding-pic">
        <img src="../assets/building.png" alt="">
      </div>
      <div class="animated fadeInRight" id="public-builiding-text">
        <div class="public-builiding-text-dis">
          <span>师傅莫急</span>
          <br>
          <span>程序猿正在拼命建设中 ……</span>
        </div>
        <div class="public-builiding-text-return">
          <div @click="home()">返回首页</div>
        </div>
      </div>
    </div>
  </div>
</template>

<style lang="less" scoped>
  .public-builiding {
    position: relative;
    /*min-height: 100vh;*/
    width: 100%;
    font-family: 思源黑体;

    #public-builiding-pic {
      width: 60%;
      position: relative;
      float: left;
      margin-top: 7%;
      img {
        width: 100%;
      }
    }

    #public-builiding-text {
      width: 40%;
      position: relative;
      float: right;
      height: 100%;
      margin-top: 21%;
      .public-builiding-text-dis{
        position: relative;
        font-weight: bold;
        font-size: .21rem;
        font-style: oblique;
        line-height: 77px;
      }
      .public-builiding-text-return{
        position: relative;
        font-size: .12rem;
        top: 9vh;
        div{
          position: relative;
          left: 38%;
          padding: 12px 10px;
          color: #404040;
          border: 2px solid #404040;
          width: 9vw;
          text-align: center;
          border-radius: 50px;
          transition: all .3s;
          cursor: pointer;
        }
        div:hover{
          background: linear-gradient(-45deg,#5DD9F4,#6256EB);
          color: white;
          border: none;
        }
      }
    }
  }
</style>

<script>
  export default {
    name: "public-building",
    methods: {
      home() {
        const home = this;
        home.$router.push({name: "Index"})
      },
    }
  }
</script>



