<template>
  <div class="header" id="header-sty">
    <ul>
      <li style="padding: 0 60px 15px 10px"><img style="width: .8rem;height: .27rem;"
                                                 src="../assets/aemelogo.png"
                                                 alt=""></li>
      <li><a href="#" @click="home()">首页</a></li>
      <li><a href="#" @click="building()">产品中心</a></li>
      <li><a href="#" @click="building()">项目案例</a></li>
      <li><a href="#" @click="building()">资讯中心</a></li>
      <li><a href="#" @click="aboutaeme()">关于航电</a></li>
    </ul>
  </div>
</template>

<script>
  import '../style/css/animate.min.css'
  import '../style/js/jquery-1.8.3.min'
  //下滑隐藏，上滑出现js
  import '../style/js/jquery-2.1.4.min'

  export default {
    name: "public-header",
    methods:{
      home(){
        const home = this;
        home.$router.push({name:"Index"})
      },
      aboutaeme(){
        const aboutus = this;
        aboutus.$router.push({name: "AboutAEME"})
      },
      building(){
        const building = this;
        building.$router.push({name:"Building"})
      }
    }
  }

  //下滑隐藏，上滑出现fun
  $(function () {
    let oTop1 = $(document).scrollTop();

    $(window).scroll(function () {
      let oTop2 = $(document).scrollTop();

      if (oTop2 > oTop1) {
        $(".header").addClass("hide").removeClass("show");
      } else {
        $(".header").addClass("show").removeClass("hide");
      }
      oTop1 = $(document).scrollTop();
    });
  });


  //到指定距离改变样式
  function newheaderstyle(obj, type, fn) {
    if (obj.attachEvent) {
      obj.attachEvent('on' + type, function () {
        fn.call(obj);
      })
    } else {
      obj.addEventListener(type, fn, false);
    }
  }

  newheaderstyle(window, 'scroll', function () {
    let scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
    if (scrollTop >= document.documentElement.clientHeight) { //滚动条下拉当前窗口高度时触发样式表
      $(".header").addClass("new-sty-b-s").removeClass("new-sty-b-h");
    } else {
      $(".header").addClass("new-sty-b-h").removeClass("new-sty-b-s");
    }
  });
</script>

<style lang="less" scoped>
  .new-sty-b-h {
  }

  .header ul li a:hover {
    border-bottom: 2px solid #404040;
  }

  .header ul li a {
    /*display: block; !* 将链接设为块级元素 *!*/
    color: white;
    padding: 15px 0;
    font-weight: bold;
    text-decoration: none; /* 去掉下划线 */
    transition: all .5s;
  }

  .header ul li {
    float: left;
    padding: 2px 50px;
    list-style: none;
  }

  .header:hover {
    background: #fff;
    -webkit-filter: drop-shadow(0 0 15px #f1f1f1);
    filter: drop-shadow(0 0 15px #f1f1f1);
    ul li a{
      color: #404040;
    }

  }

  .header {
    position: fixed;
    width: 100%;
    font-size: .13rem;
    font-family: 思源黑体;
    color: white;
    z-index: 99;
  }

  html,
  body {
    margin: 0;
    padding: 0;
  }

  /*下滑隐藏，上滑出现css*/
  .hide {
    transition: .5s;
    top: -150px;
  }

  .show {
    top: 0;
    transition: .5s;
  }

  .new-sty-b-s {
    background: #fff;
    -webkit-filter: drop-shadow(0 0 15px #f1f1f1);
    filter: drop-shadow(0 0 15px #f1f1f1);

    /*.header {*/
    /*  background: white;*/
    /*  color: #404040;*/
    /*  ul li a {*/
    /*    color: #404040;*/
    /*  }*/
    /*}*/

    ul li a {
      color: #404040;
      transition: all .2s;
    }

    ul li a:hover {
      /*background-image: -webkit-linear-gradient(-45deg, #5DD9F4, #6256EB);*/
      /*-webkit-background-clip: text;*/
      /*-webkit-text-fill-color: transparent;*/
      border-bottom: 2px solid #404040;
      /*border-image: -webkit-linear-gradient(#5DD9F4, #6256EB) 30 30;*/
      /*border-image: -moz-linear-gradient(#5DD9F4, #6256EB) 30 30;*/
      /*border-image: linear-gradient(#5DD9F4, #6256EB) 30 30;*/

    }
  }

</style>



